//         return this.slice(0, prefix.length) === prefix;
//     };
// }

var projectID = window.location.pathname.substring(9);

/**
 * 
 */
function initGame(): void {
    let canvas = <HTMLCanvasElement>document.getElementById('renderCanvas');
    let fpsLabel = document.getElementById('fpsLabel');
    if (canvas !== null && fpsLabel !== null) {
        game = new Game(canvas, fpsLabel);
        game.createScene(datas).animate();  // 
    }
}

function runBtn(): void {
    if (game) {
        game.createScene(datas).animate();
    }
}

function toggleDebug(): void {
    if (game) {
        game.toggleDebug();
    }
}


function init(): void {
    // 
    if (dataLoaded && dataLoaded2) {
        initGame();
    } else {
        setTimeout(init, 500);
    }
}


function loadResources(): void {

    var projectID = window.location.pathname.substring(9);
    Config.projectID = projectID;

    ossfile.getJsonConfig(projectID, "assets")
    .then(response => {
        var data=JSON.parse(response);
        Config.assetsData = data;
        BabylonLoader.assetsData = data;
        // editor.call('initAssets', Config.assetsData);
        // assetsscenesceneassets
        editor.call('getScenes');
        // 
        if(data.assets) {
            for(let key in data.assets) {
                if(data.assets[key].type === 'table') {
                    Config.tableAssetsID = key;
                    Config.tableName = data.assets[key].name;
                }
            }
        }
        if(Config.tableAssetsID !== '') {
            editor.call('loadTable');
        } else {
            dataLoaded = true;
        }
    })
    .catch(
        error => {
            console.error(error);
        }
    );


    editor.method('getScenes', () => {
        // scenes data
        ossfile.getJsonConfig(projectID, "scenes")
            .then(response => {
                var data=JSON.parse(response);
                let lastScene: number = data.last;
                Config.scenesData = data.scenes[lastScene];
                Config.sceneIndex = lastScene;
                // console.log(Config.scenesData);
                dataLoaded2 = true;
            })
            .catch(
                error => {
                    console.error(error);
                }
            );
    });


    editor.method('loadTable', () => {
        // 
        ossfile.getJsonAsset(projectID,Config.tableAssetsID,"table")
            .then(function (response) {
                let data = JSON.parse(response);
                // json
                let tempData = JSON.stringify(data);
                // 
                tempData = escape2Html(tempData);
                // json
                // hot1.loadData(JSON.parse(tempData).table);
                // console.log(JSON.parse(tempData).table);
                datas = JSON.parse(tempData).table;
                Config.tableData = datas;
                dataLoaded = true;
            })
            .catch(function (error) {
                console.log('load error: ' + error);
            });
    });



}

loadResources();




//HTML< -> &lt;
function html2Escape(sHtml: any) {
    var temp: any = document.createElement('div');
    temp.textContent != null
        ? (temp.textContent = sHtml)
        : (temp.innerText = sHtml);
    var output = temp.innerHTML;
    temp = null;
    return output;
}

//HTML&lt; -> <
function escape2Html(str: any) {
    var temp: any = document.createElement('div');
    temp.innerHTML = str;
    var output = temp.innerText || temp.textContent;
    temp = null;
    return output;
}

